.dmtoggle {
  border: none;
  background: none;
  margin: 0;
  padding: 0;
  font-size: 0.8rem;
  position: relative;
  opacity: 50%;
  z-index: 99;
  cursor: pointer;
}

.moon {
  top: 0px;
  left: -38px;
}
.sun {
  top: 0px;
  left: 22px;
}

input[type='checkbox'].dmcheck {
  top: 0px;
  left: 1px;
  width: 40px;
  height: 22px;
  background: #555;
  position: relative;
  border-radius: 15px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;
  &:before {
    position: relative;
    content: 'ðŸ”†';
    top: 2px;
    left: 3px;
    opacity: 33%;
  }
  // & + label::before {
  //   content: 'ðŸ”†';
  //   top: 3px;
  //   left: 33px;
  // }
  &:after {
    position: relative;
    top: 2px;
    left: 8px;
    content: 'ðŸŒ™';
    opacity: 33%;
  }
  // &::after {
  //   top: 6px;
  //   left: 43px;
  // }
  &:checked + label {
    left: -22px;
  }

  &:focus-visible {
    outline: solid 2px;
  }

  & + label {
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    transition: all 0.2s ease;
    cursor: pointer;
    position: relative;
    top: 0px;
    left: -40px;
    opacity: 100%;
    background-color: #f6f6f6;
    z-index: 100;
  }
}
